.App {
  text-align: center;
}

@font-face {
  font-family: '2005_iannnnnGMO';
  src: local('2005_iannnnnGMO.ttf'), url(./fonts/2005_iannnnnGMO.ttf) format('truetype');
}

.Header {
  background-image: url("./Images/thai int-01.png");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 1920px 1080px;
  max-height: 1080px;
  min-height: 1080px;
  max-width: 1920px;
  min-width: 1920px;
}

.title {
  max-height: 1080px;
  min-height: 1080px;
  max-width: 1920px;
  min-width: 1920px;
}

.head{
  background-color: blue;
}

.head2{
  background-color: black;
}

.head3{
  background-color: blueviolet;
}

.head4{
  background-color: aqua;
}


/* for header page*/
.Zoom {
  position: relative;
  bottom: -650px;
  left: -580px;
  transition: transform .2s; /* Animation */
}

.Zoom:hover {
  transform: scale(1.2); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
}

.More-Details {
  font-family: '2005_iannnnnGMO';
  background-color: azure;
  position: relative;
  bottom: -200px;
  left: 750px;
  width: 900px;
  padding: 30px;
  border-radius: 20px;
  font-size: 40px;
}

